@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@400;500;600;700&family=Heebo&display=swap");
:root {
  --font-body: "Heebo", sans-serif;
  --font-title: "Barlow", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --grey: hsl(0, 0%, 85%);
  --red: hsla(359, 94%, 62%, 1);
  --orange: hsla(21, 89%, 56%, 1);
  --yellow: hsla(33, 94%, 55%, 1);
  --green: hsla(162, 43%, 46%, 1);
  --blue: hsla(208, 25%, 45%, 1);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  width: 90%;
  max-width: 24.75rem;
  margin: 0 auto;
}
@media (min-width: 48em) {
  .container {
    display: grid;
    max-width: 90%;
    grid-template-columns: 1fr 1fr;
    gap: 1.5em;
  }
}
@media (min-width: 64em) {
  .container {
    max-width: 75rem;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background: #fafafa;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove text-decoration from a */
a {
  text-decoration: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.header {
  border-bottom: 1px solid var(--grey);
  display: flex;
  justify-content: center;
  margin-bottom: 1em;
  background: #fafafa;
}
.header h1 {
  font-family: var(--font-title);
  font-size: 1.6rem;
  text-align: center;
  padding: 0.3em 1em;
  font-weight: 600;
  border-inline: 1px solid var(--grey);
}

.card {
  border: 1px solid var(--grey);
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 1em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: var(--white);
}
.card .image-container {
  max-width: 100%;
  overflow: hidden;
  aspect-ratio: 90/66;
}
.card .image-container .image {
  transition: all 0.25s;
}
.card .image-container .image:hover {
  transform: scale(1.025);
  transition: all 0.25s;
}
.card .description-container {
  padding: 1em;
  background: var(--white);
}
.card .description-container .card-title {
  font-size: 1.3rem;
  font-family: var(--font-title);
  font-weight: 600;
  margin-bottom: 0.8em;
}
.card .description-container .languages {
  font-family: var(--font-title);
  font-weight: 700;
  margin-bottom: 0.8em;
}
.card .description-container .languages .html {
  color: var(--red);
  margin-right: 0.3em;
}
.card .description-container .languages .css {
  color: var(--yellow);
  margin-right: 0.3em;
}
.card .description-container .languages .js {
  color: var(--green);
}
.card .description-container .description {
  font-family: var(--font-body);
  color: #666666;
  font-size: 0.95rem;
  margin-bottom: 1em;
}
.card .action-buttons {
  padding: 0 1em;
  margin-bottom: 1em;
  background: var(--white);
}
.card .action-buttons .code-button {
  font-family: var(--font-title);
  text-transform: uppercase;
  font-weight: 600;
  color: var(--white);
  background: var(--red);
  padding: 0.3em 1.3em;
  border: 2px solid var(--red);
  border-radius: 100px;
  margin-right: 1em;
  transition: all 0.25s;
}
.card .action-buttons .code-button:hover {
  color: var(--red);
  background: none;
  transition: all 0.25s;
}
.card .action-buttons .preview-button {
  font-family: var(--font-title);
  text-transform: uppercase;
  font-weight: 600;
  color: var(--white);
  background: var(--blue);
  padding: 0.3em 1.3em;
  border: 2px solid var(--blue);
  border-radius: 100px;
  margin-right: 1em;
  transition: all 0.25s;
}
.card .action-buttons .preview-button:hover {
  color: var(--blue);
  background: none;
  transition: all 0.25s;
}
@media (min-width: 48em) {
  .card {
    margin-bottom: 0;
  }
}

.attribution {
  width: 100%;
  text-align: center;
  font-family: var(--font-title);
  font-size: 0.8rem;
  color: #0a0d10;
  padding: 0.3em 0;
  background: var(--blue);
}
.attribution a {
  color: #e6e6e6;
}
@media (min-width: 48em) {
  .attribution {
    margin-top: 2em;
  }
}